* {
  margin:0;
  padding: 0;
  box-sizing: border-box;
}

:root {
  --bg-color: #0A1D45;
  --color-grey: #D9D9D9;
  --button-color: #546791;
  --color-green: #28E94F;
  --font-poppins: 'Poppins';
  --font-roboto: 'Roboto';
}

.App {
  position: relative;
  height: 100%;
  width: 100%;
  scroll-behavior: smooth;
}